import { useEffect } from "react";

import useInteractiveFetchStatus, { useToggleSpinner } from "../useInteractiveFetchStatus";
import { CONFIGURATION_RELOAD_STATE } from "../../consts/configurations/configurationsActions";
import VENDOR_DISPLAY_NAME_MAPPING from "../../consts/configurations/vendorDisplayNameMapping";
import { useSaveConfiguration } from "./useConfigurationFetch";
import useCloudRoutes from "../useCloudRoutes";

const SCHEMA_PATH_METADATA_BACKUP = "#/definitions/metadataBackup";

const mapVendorsAndSchemaMessage = ({ invalidCredentials = {}, schemaValidationErrors = [] }) => {
  const vendorList = Object.keys(invalidCredentials);

  if (vendorList.length > 0) {
    const messages = vendorList.map(vendor => VENDOR_DISPLAY_NAME_MAPPING[vendor]).join(", ");
    return `Invalid credentials: ${messages}`;
  }

  if (schemaValidationErrors.length > 0) {
    const schemaErrors = schemaValidationErrors[0];
    for (const s in schemaErrors) {
      if (schemaErrors[s].schemaPath.includes(SCHEMA_PATH_METADATA_BACKUP)) {
        return `Invalid credentials: Git Integration`;
      }
    }
  }

  return undefined;
};

const useValidateAndSaveConfiguration = reloadConfigurationState => {
  const [{ isLoading, error, data }, saveConfiguration] = useSaveConfiguration();
  const { successNotification, errorNotification } = useInteractiveFetchStatus()[1];
  const { goToSummaryRoute } = useCloudRoutes();
  useToggleSpinner(isLoading);

  useEffect(() => {
    if (error !== null) errorNotification(mapVendorsAndSchemaMessage(error));
  }, [error]);

  useEffect(() => {
    if (data !== null && data.status === "successful") {
      successNotification("Configuration saved successfully");
      reloadConfigurationState({ type: CONFIGURATION_RELOAD_STATE, payload: data.configuration });
      goToSummaryRoute();
    }
  }, [data]);

  return saveConfiguration;
};

export default useValidateAndSaveConfiguration;
