import React from "react";
import PropTypes from "prop-types";
import { Switch, Route } from "react-router-dom";

import {
  ROUTE_CONFIGURATION,
  ROUTE_BASIC_CONFIGURATION,
  ROUTE_CLOUD_CLIENT,
  ROUTE_ADVANCED_CONFIGURATION,
  ROUTE_EMPTY_CONFIGURATION,
  DEDUP_SCENARIOS_PATH,
  DEDUP_SCENARIOS_DETAILS_PATH,
  DEDUP_SCENARIOS_NEW_PATH,
  DEDUP_DUPLICATES_DETAILS_PATH
} from "../../consts/addresses";

export const NestedRoutesPropTypes = {
  routes: PropTypes.arrayOf(
    PropTypes.shape({
      key: PropTypes.string.isRequired,
      path: PropTypes.oneOf([
        ROUTE_CONFIGURATION,
        ROUTE_BASIC_CONFIGURATION,
        ROUTE_CLOUD_CLIENT,
        ROUTE_ADVANCED_CONFIGURATION,
        ROUTE_EMPTY_CONFIGURATION,
        DEDUP_SCENARIOS_PATH,
        DEDUP_SCENARIOS_DETAILS_PATH,
        DEDUP_SCENARIOS_NEW_PATH,
        DEDUP_DUPLICATES_DETAILS_PATH
      ]).isRequired,
      exact: PropTypes.bool,
      component: PropTypes.elementType.isRequired
    })
  ).isRequired
};

const NestedRoutes = ({ routes = [] }) => (
  <>
    {routes.map(route => (
      <Route
        key={route.key}
        path={route.path}
        exact={route.exact}
        render={() => <route.component routes={route.routes} />}
      />
    ))}
  </>
);

NestedRoutes.propTypes = NestedRoutesPropTypes;

export default NestedRoutes;
