import { useState, useEffect } from "react";

import { ENDPOINT_VENDOR_VALIDATION } from "../../consts/addresses";
import { useToggleSpinner } from "../useInteractiveFetchStatus";
import useFetch from "../useFetch";

export const isSalesforceCredentialValid = obj => {
  const { type, provider, organizationId, ...credential } = obj;
  const values = Object.values(credential);
  if (values.length > 0 && values.find(value => value !== "")) return true;
  return false;
};

const validateVendorFetch = vendor => ({
  url: ENDPOINT_VENDOR_VALIDATION,
  method: "POST",
  data: vendor
});

const useIndividualVendorValidity = () => {
  const [{ isLoading, data, error }, setFetch] = useFetch();
  const [vendorValidity, setVendorValidity] = useState({ status: "" });
  const [vendor, setVendor] = useState(null);
  useToggleSpinner(isLoading);

  useEffect(() => {
    if (vendor === null) return;
    setFetch(validateVendorFetch(vendor));
  }, [vendor]);

  useEffect(() => {
    if (data === null) return;
    setVendorValidity(data);
  }, [data]);

  useEffect(() => {
    if (error === null) return;
    setVendorValidity(error);
  }, [error]);

  return [vendorValidity, setVendor];
};

export const useAutoDismissToast = validation => {
  const [isDismissed, setIsDismissed] = useState(true);

  useEffect(() => {
    setIsDismissed(true);
    const id = setTimeout(() => setIsDismissed(false), 3000);
    return () => clearTimeout(id);
  }, [validation]);

  return isDismissed;
};

export default useIndividualVendorValidity;
