import {
  AWS_REGIONS_US_EAST_2,
  AWS_REGIONS_US_EAST_1,
  AWS_REGIONS_US_WEST_1,
  AWS_REGIONS_US_WEST_2,
  AWS_REGIONS_AP_EAST_1,
  AWS_REGIONS_AP_SOUTH_1,
  AWS_REGIONS_AP_NORTHEAST_3,
  AWS_REGIONS_AP_NORTHEAST_2,
  AWS_REGIONS_AP_SOUTHEAST_1,
  AWS_REGIONS_AP_SOUTHEAST_2,
  AWS_REGIONS_AP_NORTHEAST_1,
  AWS_REGIONS_CA_CENTRAL_1,
  AWS_REGIONS_EU_CENTRAL_1,
  AWS_REGIONS_EU_WEST_1,
  AWS_REGIONS_EU_WEST_2,
  AWS_REGIONS_EU_WEST_3,
  AWS_REGIONS_EU_NORTH_1,
  AWS_REGIONS_ME_SOUTH_1,
  AWS_REGIONS_SA_EAST_1
} from "../../consts/regions";

const regionLabelMap = {
  [AWS_REGIONS_US_EAST_2]: "US East (Ohio)",
  [AWS_REGIONS_US_EAST_1]: "US East (N. Virginia)",
  [AWS_REGIONS_US_WEST_1]: "US West (N. California)",
  [AWS_REGIONS_US_WEST_2]: "US West (Oregon)",
  [AWS_REGIONS_AP_EAST_1]: "Asia Pacific (Hong Kong)",
  [AWS_REGIONS_AP_SOUTH_1]: "Asia Pacific (Mumbai)",
  [AWS_REGIONS_AP_NORTHEAST_3]: "Asia Pacific (Osaka-Local)",
  [AWS_REGIONS_AP_NORTHEAST_2]: "Asia Pacific (Seoul)",
  [AWS_REGIONS_AP_SOUTHEAST_1]: "Asia Pacific (Singapore)",
  [AWS_REGIONS_AP_SOUTHEAST_2]: "Asia Pacific (Sydney)",
  [AWS_REGIONS_AP_NORTHEAST_1]: "Asia Pacific (Tokyo)",
  [AWS_REGIONS_CA_CENTRAL_1]: "Canada (Central)",
  [AWS_REGIONS_EU_CENTRAL_1]: "Europe (Frankfurt)",
  [AWS_REGIONS_EU_WEST_1]: "Europe (Ireland)",
  [AWS_REGIONS_EU_WEST_2]: "Europe (London)",
  [AWS_REGIONS_EU_WEST_3]: "Europe (Paris)",
  [AWS_REGIONS_EU_NORTH_1]: "Europe (Stockholm)",
  [AWS_REGIONS_ME_SOUTH_1]: "Middle East (Bahrain)",
  [AWS_REGIONS_SA_EAST_1]: "South America (So Paulo)"
};

const options = Object.entries(regionLabelMap).map(([value, label], index) => ({
  label,
  value,
  subTitle: value,
  id: index.toString()
}));

export const getRegionLabel = region => regionLabelMap[region];

const useAwsRegions = () => {
  const getSelection = region => {
    const selection = options.find(option => option.value === region);
    if (selection !== undefined) return [selection];
    return undefined;
  };

  return [options, getSelection];
};

export default useAwsRegions;
